type EventState = {
  routeCoordinates: Array<Number>,
  clickCoordinates: Array<Number>,
  clicks: number,
  routeInstructions: Map<number, any>,
  summary: string,
  distance: number,
  durations: Array<Number>,
}

function getDefaultEventState(): EventState {
  return {
    routeCoordinates: [],
    clickCoordinates: [],
    clicks: 0,
    routeInstructions: new Map<number, any>(),
    summary: "New event",
    distance: 0,
    durations: []
  };
}

